import React, { useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import className from 'classnames/bind';
import Clickable from '~/components/Clickable';
import FormInput from '~/components/Form/FormInput';
import styles from '../Password.module.scss';
import { toast } from 'react-toastify';
import { httpResetPassword } from '../../../apiServices/accountServices';
import ValidationRegex from '~/utils/validationRegex';

const cx = className.bind(styles);
const initValue = { password: '' };

function ResetPassword() {
  const { token } = useParams();
  console.log(token);
  const navigate = useNavigate();
  const [user, setUser] = useState(initValue);
  const [validated, setValidated] = useState(false);
  const handleChange = (e) => {
    setUser({ ...user, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!validated) {
      return;
    }
    const newUser = {
      password: user.password,
    };
    const response = await httpResetPassword(token, newUser);
    if (response.errMsg) {
      toast.error(response.errMsg, {
        position: toast.POSITION.TOP_CENTER,
        autoClose: 2000,
      });
      return;
    }
    setUser(initValue);
    toast.success('Cp nht thnh cng', {
      position: toast.POSITION.TOP_CENTER,
      autoClose: 2000,
    });
    setTimeout(() => {
      navigate('/auth');
    }, 1000);
  };
  const formInputs = [
    {
      id: 1,
      type: 'password',
      name: 'password',
      placeholder: 'Mt khu',
      required: true,
      pattern: ValidationRegex.password.pattern,
      message: ValidationRegex.password.message,
    },
    {
      id: 2,
      type: 'password',
      name: 'confirmPassword',
      placeholder: 'Nhp li mt khu',
      required: true,
      pattern: (value) => value === user.password,
      message: 'Mt khu khng trng khp',
    },
  ];

  return (
    <div className={cx('container', 'wrapper')}>
      <h1>t li mt khu</h1>
      <form onSubmit={handleSubmit} className={cx('form')}>
        {formInputs.map((formInput) => (
          <FormInput
            key={formInput.id}
            value={user[formInput.name]}
            onChange={handleChange}
            setValidated={setValidated}
            {...formInput}
          />
        ))}
        <a
          onClick={(e) => {
            e.preventDefault();
            navigate('/');
          }}
          href='/'
        >
          Quay li trang ch
        </a>
        <Clickable text='Cp nht' primary />
      </form>
    </div>
  );
}

export default ResetPassword;
