import React from 'react';
import { Link } from 'react-router-dom';

const Menu = () => {
  const MENU_ITEM = [
    {
      title: 'Ca hng',
      to: '/stores',
    },
    {
      title: 'Menu',
      to: '/menu/all',
    },
    {
      title: 'Gii thiu',
      to: '/about',
    },
    {
      title: 'Lin h',
      to: '/contact',
    },
    {
      title: 'Tuyn dng',
      to: '/news',
    },
  ];
  return MENU_ITEM.map((item, index) => {
    return (
      <li key={index}>
        <Link to={item.to}>{item.title}</Link>
      </li>
    );
  });
};

export default Menu;
