const cx = className.bind(styles);

const Section = (props) => {
  const { reverse } = props;
  //directtion =
  return (
    <section className={cx('wrapper', { reserve: reverse }, 'container')}>
      <div className={cx('col', 'image')}>
        <img
          src='https://file.hstatic.net/1000075078/file/3_79de83d7fe14444780d3f11e1d07ab20_master.jpg'
          alt='section'
        />
      </div>
      <div className={cx('col', 'content')}>
        <h2>Gogi Milk Tea V Vn Ngn</h2>
        <p>
          Ca hng mi ti Thnh ph Th a to lc ng V Vn Ngn <br />{' '}
          (gn trng i hc S phm K thut TP H Ch Minh). Hng v quen
          thuc t cc thc ung ca Gogi, mang n nhiu cung bc cm xc cho
          nhng bui gp g cng bn b. Ha hn s l im n l tng cho cc
          bn tr.
        </p>
      </div>
    </section>
  );
};

export default Section;
