import React, { useEffect } from 'react';
import className from 'classnames/bind';
import styles from './About.module.scss';

import coffeeBerry from '../../assets/images/coffee-berry.jpg';
import coffeeBeans from '../../assets/images/coffee-beans.jpg';
import coffeePot from '../../assets/images/coffee-pot.jpg';
const cx = className.bind(styles);
const About = () => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className={cx('container', 'about')}>
      <div className={cx('container-fluid', 'cover')}>
        <div className={cx('story-photo')}></div>
      </div>
      <div className={cx('content')}>
        <h2>Hnh trnh ca GoGi</h2>
        <p>
          Lun tm huyt vi vic khai thc ngun nng sn Vit Nam  to ra
          nhng ly thc ung ti ngon, an ton v giu gi tr dinh dng. Gogi
          m ca hng u tin vo nm 2013, mang trong mnh lng am m v kht
          vng xy dng mt thng hiu tr sa thun Vit, mang m hng v
          qu hng GoGi tin rng thng thc mt ly tr sa c pha ch t tr
          Mc Chu, trn chu t sn dy Ngh An hay mt du tm t  Lt s l
          nhng tri nghim hon ton khc bit v tuyt vi nht cho nhng
          khch hng ca mnh.
        </p>
        <p>
          GoGi cng  tin vo sc mnh ca c ph nn cng  a c ph vo
          thc n ca mnh. Bi c ph cung cp mt s pha trn phc hp gia
          mu sc, mi thm v hng v, gip nh thc chng ta, l ngun cung
          cp cht chng oxy ha ln trong ch  n ung v bo v chng li
          cc bnh khc nhau.  l mt thc ung k diu, phi khng?  l l
          do ti sao chng ti thch phc v iu ny.
        </p>
        <p>
          Hnh trnh y am m v tm huyt ny s tip tc nhn rng  lan
          ta nhng ly tr thun khit nng sn Vit n mi min trn t nc
          Vit Nam.
        </p>
      </div>
      <div className={cx('container', 'gallery')}>
        <img src={coffeeBerry} alt='Coffee berry' />
        <img src={coffeeBeans} alt='Coffee beans' />
        <img src={coffeePot} alt='Coffee pot' />
      </div>
    </div>
  );
};

export default About;
