import React, { useEffect, useState } from 'react';
import className from 'classnames/bind';
import styles from './Form.module.scss';
import { useNavigate, useParams } from 'react-router-dom';
import FormValidation from '~/components/Form/FormValidation';
import FormInput from '~/components/Form/FormInput';
import ValidationRegex from '~/utils/validationRegex';
import Clickable from '~/components/Clickable';
import {
  httpGetVoucherById,
  httpPostVoucher,
  httpPutVoucher,
} from '~/apiServices/voucherService';
import moment from 'moment-timezone';

const cx = className.bind(styles);
const initValue = {
  name: '',
  value: 0,
  startDate: '',
  endDate: '',
  code: '',
  minimumOrderValue: 0,
  maximumDiscountAmount: 0,
};
function Voucher() {
  const { id } = useParams();
  const navigate = useNavigate();
  const [voucher, setVoucher] = useState(initValue);

  useEffect(() => {
    if (id === 'add') {
      return;
    } else {
      getVoucherById();
    }
  }, [id]);

  const getVoucherById = async () => {
    const response = await httpGetVoucherById(id);
    console.log(response.data);
    setVoucher(response.data);
  };

  const handleChange = (e) => {
    setVoucher((prev) => {
      return { ...prev, [e.target.name]: e.target.value };
    });
  };

  const handleSubmit = async (e, formValidated) => {
    try {
      e.preventDefault();
      if (!formValidated) {
        return;
      }
      if (id === 'add') {
        const res = await httpPostVoucher(voucher);
        if (res.data) {
          console.log(res.data);
        } else console.log(res.errMsg);
      } else {
        const res = await httpPutVoucher(voucher.id, voucher);
        if (res.data) {
          console.log(res.data);
        } else console.log(res.errMsg);
      }
    } catch (error) {
      console.log(error);
    }
    navigate('/admin/vouchers');
  };
  const formInputs = [
    {
      id: 1,
      name: 'name',
      title: 'Tn m gim gi',
      type: 'text',
      placeholder: 'VD: u i khch hng mi',
      required: true,
      pattern: ValidationRegex.name.pattern,
      message: ValidationRegex.name.message,
    },
    {
      id: 2,
      name: 'value',
      title: 'Gi tr',
      type: 'number',
      placeholder: 'VD: 0.1',
      required: true,
    },
    {
      id: 3,
      name: 'code',
      title: 'M code',
      type: 'text',
      placeholder: 'VD: KHACHMOI',
      required: true,
      pattern: ValidationRegex.code.pattern,
      message: ValidationRegex.code.message,

    },
    {
      id: 4,
      name: 'minimumOrderValue',
      title: 'n ti thiu',
      type: 'number',
      placeholder: 'VD: 5000',
      required: true,
      pattern: ValidationRegex.price.pattern,
      message: ValidationRegex.price.message,

    },
    {
      id: 1,
      name: 'maximumDiscountAmount',
      title: 'Gim ti a',
      type: 'text',
      placeholder: 'VD: 10000',
      required: true,
      pattern: ValidationRegex.price.pattern,
      message: ValidationRegex.price.message,

    }
  ];
  return (
    <div className={cx('wrapper')}>
      <FormValidation>
        {({ setValidated, formValidated }) => (
          <form
            onSubmit={(e) => handleSubmit(e, formValidated)}
            className={cx('form')}
          >
            <h1>M gim gi</h1>

            {formInputs.map((formInput) => (
              <FormInput
                key={formInput.id}
                value={voucher[formInput.name]}
                onChange={handleChange}
                setValidated={setValidated}
                {...formInput}
              />
            ))}
            <label>Ngy bt u</label>
            <input
              type='datetime-local'
              name='startDate'
              value={moment(voucher.startDate).format('YYYY-MM-DDTHH:mm')}
              onChange={handleChange}
              required
            />

            <label>Ngy kt thc</label>
            <input
              type='datetime-local'
              name='endDate'
              value={moment(voucher.endDate).format('YYYY-MM-DDTHH:mm')}
              onChange={handleChange}
              required
            />
            <span className={cx('height')} />
            <Clickable text='Gi' primary />
          </form>
        )}
      </FormValidation>
      
    </div>
  );
}
export default Voucher;
