export const formatPrice = (price) => {
  const formatter = new Intl.NumberFormat('vi-VN', {
    style: 'currency',
    currency: 'VND',
  });
  if (isNaN(price)) {
    return formatter.format(0);
  }
  return formatter.format(price);
};

export const getSplitAddress = (fullAddress) => {
  const indexDistrictStart = fullAddress.includes('huyn')
    ? fullAddress.indexOf('huyn')
    : fullAddress.includes('qun')
    ? fullAddress.indexOf('qun')
    : fullAddress.indexOf('thnh ph');
  const indexProvinceStart = fullAddress.includes('tnh')
    ? fullAddress.indexOf('tnh')
    : fullAddress.indexOf('thnh ph');
  const streetName = fullAddress.slice(0, indexDistrictStart - 2);
  const districtName = fullAddress.slice(
    indexDistrictStart,
    indexProvinceStart - 2
  );
  const provinceName = fullAddress.slice(indexProvinceStart);
  return { streetName, districtName, provinceName };
};

export const formatPercent = (percent) => {
  return percent * 100 + '%';
};

export const capitalizeFirstLetter = (string) => {
  return string ? string.charAt(0).toUpperCase() + string.slice(1) : '';
};
